@import url('https://fonts.googleapis.com/css2?family=Knewave&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Qwigley&display=swap');
* {
	box-sizing: border-box;
	background-color: rgb(244, 244, 244);
	color: #333;
	font-size: 10px;
}
.heading {
	font-family: 'Knewave', cursive;
	font-size: 4rem;
	color: rgb(102, 78, 78);
	text-align: center;
}
.App {
	margin: 0;
	padding: 0;
}

.label {
	font-size: 3rem;
	margin-bottom: 1rem;
}

.container {
	margin: 0 auto;
	max-width: 100vw;
	overflow-x: hidden;
	padding: 40px;
}

.title {
	font-size: 4.4rem;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.subt {
	font-size: 2.3rem;
	color: rgb(173, 42, 42);
	text-align: center;
	font-family: 'Qwigley', cursive;
}

.button {
	background-color: rgba(247, 244, 244, 0.75);
	color: white;
	padding: 1rem 3rem;
	border: 1px solid rgba(0, 0, 0, 0.75);
	border-radius: 10px;
	font-size: 2rem;
	cursor: pointer;
	color: #333;
	transition: background-color 250ms;
}

.button:hover {
	background-color: rgba(0, 0, 0, 0.85);
	color: #eee;
}

.form {
	display: grid;
}

.input {
	font-size: 1.6rem;
	padding: 0.5rem 2rem;
	line-height: 2.8rem;
	border-radius: 20px;
	background-color: white;
	margin-bottom: 1rem;
}

.card {
	margin-bottom: 1rem;
	display: flex;
}

.card--image {
	flex: 100%;
	margin-top: 1rem;
	border-radius: 10px;
}

.card-list {
	column-count: 3;
}

@media (min-width: 768px) {
	.form {
		grid-template-columns: auto 1fr auto;
		grid-gap: 1rem;
		align-items: center;
	}
	.input {
		margin-bottom: 0;
	}
}

@media only screen and (max-width: 600px) {
	.card-list {
		column-count: 1;
	}
}
